body {
  padding: 0;
  margin: 0;
  font-family: 'Arsenal', sans-serif;
  color: #235466;
  box-sizing: border-box;
}
.main__wrapper {
  max-width: 1920px;
  margin: 0 auto;
}
.wrapper {
  max-width: 1170px;
  margin: 0 auto;
}
a {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: inherit;
}
button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: #fff;
  color: #235466;
}
button:hover {
  cursor: pointer;
}
button:focus {
  outline: none;
}
.ellipse_top {
  background-image: url(../../img/Slice_1.png);
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% auto;
}
.ellipse_bottom {
  background: url(../../img/Slice_2.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% auto;
}
.ellipse-fix {
  position: relative;
  top: -100px;
}
ul {
  padding: 0;
  margin: 0;
}
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.header {
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  width: 95%;
  box-sizing: border-box;
  z-index: 2;
}
.header-nav__item {
  text-transform: uppercase;
  margin-right: 70px;
  font-size: 14px;
  font-weight: 700;
  position: relative;
}
.header-nav__item:hover:after {
  content: '';
  display: inline-block;
  position: absolute;
  height: 2px;
  background-color: #235466;
  width: 100%;
  bottom: -10px;
  left: 0;
}
.burger-menu {
  display: none;
  flex-direction: column;
}
.burger-menu span {
  content: '';
  width: 23px;
  height: 3px;
  background-color: #235466;
  display: inline-block;
  margin: 3px;
  border-radius: 25%;
}

.intro {
  position: relative;
}
.slider {
  position: relative;
}
.slide1 {
  background: url(../../img/slide1.png) no-repeat center center;
}
.slide2 {
  background: url(../../img/slide2.png) no-repeat center center;
}
.slide {
  display: none;
  opacity: 0;
  height: 100vh;
  width: 82%;
  background-size: cover;
  margin-left: auto;
  position: relative;
  overflow: hidden;
  transition: 1s;
}
.slide .slide-scale {
  z-index: 1;
  position: absolute;
  right: 0;
  bottom: -5%;
  width: 73%;
  animation: focus 4s infinite linear;
}
@keyframes focus {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(0.95);
  }
}
.active {
  display: block;
  opacity: 1;
}
.slide__title {
  position: absolute;
  left: 28%;
  top: 52%;
}
.slide__title h2 {
  font-size: 38px;
  font-weight: 700;
  padding: 0;
  margin: 0;
}
.slide__title:after {
  content: '';
  display: flex;
  width: 100%;
  height: 40px;
  background: #235466;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  animation: text-show 4s ease;
}
@keyframes text-show {
  from {
    width: 0%;
    left: 0;
  }
  25% {
    width: 100%;
    left: 0;
  }
  35% {
    width: 0%;
    left: 100%;
  }
  to {
    width: 0%;
    left: 100%;
  }
}
.slider-control {
  width: 60px;
  height: 60px;
  border: 2px solid #235466;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 80px;
  z-index: 4;
}
.slider-control:hover {
  background: rgba(35, 84, 102, 0.2);
}
.slider_prev {
  left: 25%;
  transform: rotate(180deg);
}
.slider_next {
  left: calc(25% + 90px);
}
.sidebar-nav {
  transform: rotate(270deg);
  left: -60px;
  display: inline-block;
  position: absolute;
  top: 30%;
}
.sidebar-nav a {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  margin-right: 50px;
}
.sidebar-nav a:hover {
  text-shadow: 0 0 1px;
}
.sidebar-social {
  display: flex;
  flex: none;
  flex-direction: column;
  position: absolute;
  bottom: 7%;
  left: 92px;
}
.sidebar-social a {
  margin-bottom: 32px;
  text-align: center;
}

.about {
  padding: 150px 0 216px;
  display: flex;
  justify-content: space-between;
}
.about__heading {
  width: 36.7%;
}
.about__heading h3 {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
}
.about__heading h2 {
  font-size: 22px;
  font-weight: 700;
}
.about-text {
  width: 47.9%;
}
.about-text p {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.8;
  margin: 50px 0 65px;
}
.about-text span {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}

.careers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 540px;
}
.careers__img {
  width: 61.7%;
}
.careers__img img {
  width: 100%;
  height: auto;
}
.careers-heading {
  text-align: center;
  margin: 0 50px;
  width: 30%;
}
.careers-heading h3 {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
}
.careers-heading .careers-heading__txt {
  margin: 30px 0 40px;
  font-size: 38px;
  font-weight: 700;
  line-height: 1.2;
}
.careers-heading .careers-heading-invite {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}

.contact {
  padding-bottom: 165px;
}
.contact__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 164px;
  margin: 38px 0 60px;
}
.contact-address {
  font-size: 14px;
  font-weight: 400;
}
.contact-address h4 {
  font-size: 22px;
  font-weight: 700;
  margin: 0 0 12px;
  line-height: 1;
}
.contact-address p {
  margin-bottom: 30px;
}
.contact-address div {
  margin-bottom: 30px;
}
.contact-address a:hover {
  text-decoration: underline;
}
.contact-menu {
  margin: 0 25px;
}
.contact-menu ul {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-height: 164px;
}
.contact-menu h4 {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 12px;
  line-height: 1;
}
.contact-menu a {
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
  margin-right: 100px;
}
.contact-menu a:hover {
  text-decoration: underline;
}
.contact-map__link span {
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-transform: uppercase;
  margin-left: 17px;
}

.news__wrap {
  padding: 300px 0 400px;
  display: flex;
  justify-content: space-between;
}
.news {
  width: 30.4%;
}
.news h4 {
  font-size: 12px;
  font-weight: 700;
}
.news h3 {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
  margin: 55px 0 20px;
}
.news time {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 55px;
  display: block;
}
.news p {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
  margin-bottom: 55px;
  height: 108px;
  overflow: hidden;
}
.news-control button {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}
.news-control button:hover {
  text-decoration: underline;
}
.news-read-more:after {
  content: '';
  display: inline-block;
  position: relative;
  top: 5px;
  width: 1px;
  height: 22px;
  background-color: #809faf;
  margin: 0 37px;
}
.blog {
  width: 21.7%;
}
.blog h3 {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 60px;
}
.blog a {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
}
.blog__article {
  margin-bottom: 50px;
}
.blog__article time {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  margin-bottom: 25px;
}
.blog__article p {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.2;
}
.news-photo__wrap {
  width: 23%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.news-photo__wrap .news-image__box {
  position: relative;
}
.news-photo__wrap .news-image__box img {
  width: 100%;
  height: auto;
}
.news-photo__time {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.news-photo__time time {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
}

.references {
  margin-bottom: 200px;
}
.references-slide {
  display: flex;
  max-height: 670px;
  overflow: hidden;
}
.ref-slide_left {
  width: 61.2%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ref-slide_left .ref-slide__item {
  position: relative;
  display: none;
  height: 100%;
  width: auto;
}
.ref-slide_right {
  width: 28.39%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.ref-slide_right .ref-slide__item {
  position: relative;
  width: auto;
  height: 100%;
  display: none;
}
.ref-slide__control {
  width: 10.4%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
}
.ref-slide__count {
  font-size: 38px;
  font-weight: 700;
}
.ref-slide__next {
  margin: 60px 0;
}
.ref-slide_right .ref__active,
.ref-slide_left .ref__active {
  display: block;
}
.references__title {
  transform: rotate(270deg);
  display: inline-block;
  z-index: 1;
  position: relative;
  top: -300px;
}
.references__title h2 {
  font-weight: 700;
  color: #d7e9ed;
  font-size: 160px;
  padding: 0;
  margin: 0;
  line-height: 1;
}


.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 96px;
  background-color: #235466;
}
.footer p {
  color: #809faf;
}
.footer .footer_left {
  margin-left: 146px;
}
.footer .footer_right {
  margin-right: 160px;
}
.footer .footer_right span {
  color: #d9dcde;
}

@media (max-width: 1200px) {
  .header {
    width: 100%;
  }
  .header-nav__item:last-child {
    margin-right: 0;
  }
  .about {
    padding: 150px 40px 216px;
  }
  .news__wrap {
    padding: 300px 40px 400px;
  }
  .contact {
    padding: 0 40px 165px;
  }
  .references__title {
    left: -275px;
  }
  .references__title h2 {
    font-size: 135px;
  }
  .footer .footer_left {
    margin-left: 80px;
  }
  .footer .footer_right {
    margin-right: 90px;
  }
}
@media (max-width: 992px) {
  .header-nav__item {
    margin-right: 35px;
  }
  .sidebar-nav {
    display: none;
  }
  .slide {
    width: 100%;
    max-height: 750px;
  }
  .slider-control {
    bottom: 5%;
  }
  .slider_prev {
    left: 13%;
  }
  .slider_next {
    left: calc(13% + 80px);
  }
  .slide__title {
    left: 15%;
  }
  .slide__title h2 {
    font-size: 32px;
  }
  .header {
    padding: 30px;
  }
  .sidebar-social {
    left: 40px;
    bottom: 5%;
  }
  .sidebar-social a {
    margin-bottom: 25px;
  }
  .about {
    padding: 130px 30px 170px;
  }
  .about__heading h2 {
    font-size: 20px;
  }
  .references {
    margin-bottom: 90px;
  }
  .references-slide {
    max-height: 550px;
  }
  .references__title h2 {
    font-size: 110px;
  }
  .references__title {
    left: -240px;
  }
  .news__wrap {
    padding: 30px 30px 280px;
    display: block;
  }
  .news {
    width: 50%;
    margin: 80px auto;
  }
  .blog {
    width: 50%;
    margin: 80px auto;
  }
  .blog h3 {
    margin-bottom: 55px;
  }
  .news-photo__wrap {
    width: 50%;
    margin: 0 auto;
  }
  .careers {
    flex-direction: column;
    justify-content: flex-start;
    padding-bottom: 220px;
  }
  .careers-heading {
    width: 50%;
    margin: 0 0 50px;
    order: -1;
  }
  .careers-heading .careers-heading__txt {
    font-size: 35px;
    margin: 25px 0 32px;
  }
  .careers__img {
    width: 90%;
  }
  .contact {
    padding: 0 30px 80px;
  }
  .footer {
    height: 80px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px;
  }
  .burger-menu {
    display: flex;
  }
  .header-nav {
    display: none;
    position: absolute;
    top: 50px;
    right: 14px;
    flex-direction: column;
    border: 2px solid #2e3b40;
    padding: 15px;
    z-index: 10;
    background-color: rgba(255, 255, 255, 0.9);
    text-align: center;
  }
  .header-nav__item {
    margin-right: 0;
    margin-bottom: 10px;
    color: #2e3b40;
  }
  .header-nav__item:last-child {
    margin-bottom: 0;
  }
  .header-nav__item:hover:after {
    background-color: #2e3b40;
    bottom: -5px;
  }
  .header-nav__active {
    display: flex;
  }
  .header__logo img {
    width: 66px;
  }
  .slide {
    max-height: 480px;
  }
  .slider-control {
    width: 40px;
    height: 40px;
  }
  .slider_prev {
    left: 7%;
  }
  .slider_next {
    left: calc(7% + 50px);
  }
  .slide__title {
    left: 6%;
  }
  .slide__title h2 {
    font-size: 22px;
  }
  .slide__title::after {
    height: 30px;
  }
  .sidebar-social {
    position: static;
    flex-direction: row;
    justify-content: center;
  }
  .sidebar-social a {
    margin: 16px;
  }
  .about {
    display: block;
    padding: 65px 30px 85px;
  }
  .about__heading {
    width: 100%;
  }
  .about-text {
    width: 100%;
  }
  .references {
    margin-bottom: 40px;
  }
  .references-slide {
    flex-direction: column;
    max-height: 800px;
  }
  .ref-slide_left {
    width: 100%;
  }
  .ref-slide_right {
    width: 100%;
    max-height: 380px;
  }
  .references__title {
    transform: none;
    position: static;
  }
  .references__title h2 {
    font-size: 70px;
    text-align: center;
  }
  .ref-slide__control {
    width: 100%;
    flex-direction: row;
    justify-content: center;
  }
  .ref-slide__next {
    margin: 25px;
    padding-top: 7px;
  }
  .ref-slide__count {
    line-height: 1;
  }
  .ref-slide_right .ref-slide__item {
    width: 100%;
    height: auto;
  }
  .ref-slide_left .ref-slide__item {
    width: 100%;
    height: auto;
  }
  .blog,
  .news {
    width: 80%;
    margin-top: 30px;
  }
  .news-photo__wrap {
    width: 80%;
  }
  .careers {
    padding-bottom: 140px;
  }
  .careers-heading {
    width: 80%;
  }
  .careers__img {
    width: 99%;
  }
  .contact {
    padding: 0 30px;
  }
  .contact__logo img {
    width: 77px;
  }
  .contact-menu a {
    margin-right: 30px;
  }
  .contact__wrap {
    display: block;
    max-height: none;
    margin-bottom: 38px;
  }
  .contact-menu {
    margin: 38px 0;
  }
  .contact-menu ul {
    max-height: 100px;
  }
  .footer .footer_left {
    margin-left: 30px;
  }
  .footer .footer_right {
    margin-right: 35px;
  }
  .footer {
    height: 60px;
  }
  .footer p {
    font-size: 12px;
  }
}
@media (max-width: 479px) {
  .ellipse_top {
    background-image: none;
  }
  .slide {
    max-height: 300px;
  }
  .slide__title {
    left: 3%;
  }
  .slide__title h2 {
    font-size: 17px;
  }
  .slider-control {
    width: 30px;
    height: 30px;
  }
  .slider-control img {
    width: 8px;
  }
  .slider_next {
    left: calc(7% + 38px);
  }
  .references__title h2 {
    font-size: 50px;
  }
  .about {
    padding: 30px 10px 40px;
  }
  .about__heading h2 {
    font-size: 16px;
  }
  .about-text p {
    margin: 25px 0 30px;
    font-size: 12px;
  }
  .about-text span {
    font-size: 12px;
  }
  .ref-slide__count {
    font-size: 26px;
  }
  .ref-slide__next {
    padding-top: 5px;
    margin: 15px 25px;
  }
  .ref-slide__next img {
    height: 18px;
  }
  .ref-slide_right {
    max-height: 200px;
  }
  .news__wrap {
    padding: 30px 10px 200px;
  }
  .blog,
  .news {
    width: 100%;
    margin: 60px 0;
  }
  .news-photo__wrap {
    width: 100%;
  }
  .news h3 {
    font-size: 18px;
    margin: 34px 0 16px;
  }
  .news time {
    margin-bottom: 24px;
  }
  .news p {
    font-size: 12px;
    height: 112px;
    margin-bottom: 30px;
  }
  .news-control button {
    font-size: 12px;
  }
  .news-read-more:after {
    margin: 0 20px;
  }
  .blog__article {
    margin-bottom: 30px;
  }
  .blog__article p {
    font-size: 20px;
  }
  .blog h3 {
    margin-bottom: 38px;
  }
  .blog a {
    font-size: 12px;
  }
  .careers {
    padding-bottom: 100px;
  }
  .careers-heading {
    width: 100%;
  }
  .careers-heading .careers-heading__txt {
    font-size: 30px;
    margin: 20px 0 26px;
  }
  .careers-heading .careers-heading-invite {
    font-size: 12px;
  }
  .careers-heading h3 {
    font-size: 11px;
  }
  .contact {
    padding: 10px;
  }
  .contact__logo img {
    width: 66px;
  }
  .contact-address div,
  .contact-address p {
    margin-bottom: 14px;
  }
  .contact-map__link span {
    margin-left: 5px;
  }
  .footer .footer_left {
    margin-left: 5px;
  }
  .footer .footer_right {
    margin-right: 10px;
  }
  .footer {
    height: 55px;
  }
}
